import React, { memo, useCallback, useRef } from 'react';
import dynamic from 'next/dynamic';
import { useCollaborationMode } from '@containers/Home/global/TourStats';
import LoadingContainer from '@components/LoadingContainer';
import { DialogCreateCommentSceneWrapper } from './styled';

const CommentBox = dynamic(() => import('./CommentBox'));
const DialogContainer = dynamic(() => import('@components/DialogContainer'));

interface Props {
  visible: boolean;
  onCancel: () => void;
  onCreateCommentScene: (data, krpanoInstance) => void;
}

const DialogCreateCommentScene = ({
  visible,
  onCancel,
  onCreateCommentScene,
}: Props) => {
  const ref = useRef<HTMLDivElement>(null!);
  const [loading] = useCollaborationMode('loading');
  const [krpanoInstance] = useCollaborationMode('krpanoRef');

  const _onCreateCommentScene = useCallback(
    data => onCreateCommentScene(data, krpanoInstance),
    [krpanoInstance, onCreateCommentScene],
  );

  return (
    <DialogContainer
      visible={visible}
      onCancel={onCancel}
      maskClosable={false}
      centered
      width="500px"
      bodyStyle={{ maxWidth: '500px', width: '100%' }}
      hiddenCloseButton
    >
      <LoadingContainer loading={loading}>
        <DialogCreateCommentSceneWrapper ref={ref}>
          <i
            aria-hidden
            className="fal fa-times --icon-close"
            onClick={onCancel}
          />

          <div className="header">
            <div className="title">New Comment</div>
          </div>
          <div className="popover">
            <CommentBox
              initValue=""
              isShowBtnReset
              onCancel={() => {}}
              isShowBtnCancel={false}
              onSubmit={_onCreateCommentScene}
            />
          </div>
        </DialogCreateCommentSceneWrapper>
      </LoadingContainer>
    </DialogContainer>
  );
};
export default memo(DialogCreateCommentScene);
